
[x-placement^=left]{margin-right:10px
}
[x-placement^=left] .tooltip__arrow{right:-5px;border-right-width:0;border-top-color:transparent;border-bottom-color:transparent
}
[x-placement^=right]{margin-left:10px
}
[x-placement^=right] .tooltip__arrow{left:-5px;border-left-width:0;border-top-color:transparent;border-bottom-color:transparent
}
[x-placement^=top]{margin-bottom:10px
}
[x-placement^=top] .tooltip__arrow{bottom:-5px;border-bottom-width:0;border-left-color:transparent;border-right-color:transparent
}
[x-placement^=bottom]{margin-top:10px
}
[x-placement^=bottom] .tooltip__arrow{top:-5px;border-top-width:0;border-left-color:transparent;border-right-color:transparent
}
.tooltip__content{background:#ccc;padding:10px
}
.tooltip__arrow{position:absolute;display:block;width:0;height:0;border-color:#ccc;border-style:solid;border-width:5px
}


